import GeoJSON from 'ol/format/GeoJSON';
import Map from 'ol/Map';
import View from 'ol/View';
import {DragBox, Select} from 'ol/interaction';
import {OSM, Vector as VectorSource} from 'ol/source';
import {Tile as TileLayer, Vector as VectorLayer} from 'ol/layer';
import {platformModifierKeyOnly} from 'ol/events/condition';
import {Style,Stroke,Fill} from 'ol/style';
import XYZ from 'ol/source/XYZ';
import {getBottomLeft, getTopRight} from 'ol/extent';
import {toLonLat} from 'ol/proj';



const vectorSource1 = new VectorSource({
  url: 'https://raw.githubusercontent.com/singhskt/geojson-data/main/data001.geojson',
  format: new GeoJSON()
});

const vectorSource2 = new VectorSource({
  url: 'https://raw.githubusercontent.com/singhskt/geojson-data/main/data002.geojson',
  format: new GeoJSON()
});

const vectorSource3 = new VectorSource({
  url: 'https://raw.githubusercontent.com/singhskt/geojson-data/main/data003.geojson',
  format: new GeoJSON()
});


const vectorSource4 = new VectorSource({
  url: '',
  format: new GeoJSON()
});

const vectorLayer=new VectorLayer({
  source: vectorSource3,
});
var url3='https://api.digitalslidearchive.org/api/v1/item/5b9ef9c8e62914002e94771e/tiles/zxy/{z}/{x}/{y}?edge=crop'
const map = new Map({
  layers: [
    new TileLayer({
      source: new XYZ({
        url:url3,
}),
    }),
    vectorLayer
    ,
  ],
  target: 'map',
  view: new View({
    center: [40,80],
    zoom: 2,
    constrainRotation: 16,
  }),
});

// a normal select interaction to handle click
const select = new Select();
map.addInteraction(select);

const selectedFeatures = select.getFeatures();

// a DragBox interaction used to select features by drawing boxes
const dragBox = new DragBox({
  condition: platformModifierKeyOnly,
});

map.addInteraction(dragBox);

dragBox.on('boxend', function () {
  // features that intersect the box geometry are added to the
  // collection of selected features

  // if the view is not obliquely rotated the box geometry and
  // its extent are equalivalent so intersecting features can
  // be added directly to the collection
  const rotation = map.getView().getRotation();
  const oblique = rotation % (Math.PI / 2) !== 0;
  const candidateFeatures = oblique ? [] : selectedFeatures;
  const extent = dragBox.getGeometry().getExtent();
  vectorSource.forEachFeatureIntersectingExtent(extent, function (feature) {
    candidateFeatures.push(feature);
  });

  // when the view is obliquely rotated the box extent will
  // exceed its geometry so both the box and the candidate
  // feature geometries are rotated around a common anchor
  // to confirm that, with the box geometry aligned with its
  // extent, the geometries intersect
  if (oblique) {
    const anchor = [0, 0];
    const geometry = dragBox.getGeometry().clone();
    geometry.rotate(-rotation, anchor);
    const extent = geometry.getExtent();
    candidateFeatures.forEach(function (feature) {
      const geometry = feature.getGeometry().clone();
      geometry.rotate(-rotation, anchor);
      if (geometry.intersectsExtent(extent)) {
        selectedFeatures.push(feature);
      }
    });
  }
});

// clear selection when drawing a new box and when clicking on the map
dragBox.on('boxstart', function () {
  selectedFeatures.clear();
});

const infoBox = document.getElementById('info');



selectedFeatures.on(['add', 'remove'], function () {
  const names = selectedFeatures.getArray().map(function (feature) {
    document.getElementById("cname").value=feature.get('name');
    document.getElementById("ccustom").value=feature.get('custom');
    return JSON.stringify({
        name:feature.get('name'),
        id:feature.id_,
        custom:feature.get('custom'),
    });
  });
  if (names.length > 0) {
    infoBox.innerHTML = names.join(', ');
  } else {
    infoBox.innerHTML = 'No polgon selected';
  }
});
var form=document.getElementById("testForm")


form.addEventListener('submit',function(e){
    e.preventDefault();
    var feature=selectedFeatures.getArray()[0];
    feature.setProperties({
        name:document.getElementById("cname").value,
        custom:document.getElementById("ccustom").value
    })
    var color=document.getElementById("color").value;
    if (color!=''){
        var style = new Style({
            stroke: new Stroke({ color: color }),
        });

        feature.setStyle(style);
        

    }
    console.log(feature);
    return false;
})

var currZoom = map.getView().getZoom();

function wrapLon(value) {
  const worlds = Math.floor((value + 180) / 360);
  return value - worlds * 360;
}
map.on('moveend', function(e) {

  // this code section is to demonstrate rendering of polygons 
  // based on zoom level

  var newZoom = map.getView().getZoom();
  if (currZoom != newZoom) {
    // console.log('zoom end, new zoom: ' + newZoom);
    currZoom = newZoom;
    if (currZoom<=3){
      console.log('Vector soruce 1');

      vectorLayer.setSource(vectorSource1);
    }
    else{
      console.log('Vector soruce 2');
      vectorLayer.setSource(vectorSource2);
    }
  }
  else{  
    // const map = e.map;
    const extent = map.getView().calculateExtent(map.getSize());
    const bottomLeft = toLonLat(getBottomLeft(extent));
    const topRight = toLonLat(getTopRight(extent));
    console.log('left', wrapLon(bottomLeft[0]));
    console.log('bottom', bottomLeft[1]);
    console.log('right', wrapLon(topRight[0]));
    console.log('top', topRight[1]);


    // uncomment this code section to demonstrate rendering of polygons 
    // based on current viewport

    if (bottomLeft[1]>45){
      vectorLayer.setSource(vectorSource4);
    }
    else{
        vectorLayer.setSource(vectorSource3);
    }
  }


});

